export function formatDate(text) {
    const dateString = text.split("T")[0].split("-");
    const timeString = text.split("T")[1].split(".")[0].split(":");

    const date = dateString[2] + "." + dateString[1] + "." + dateString[0];
    const time = (parseInt(timeString[0]) + 1) + ":" + timeString[1];

    return date + ", " + time;
}

// returns TRUE if the user is logged, otherwise FALSE
export function isLogged() {
    if (localStorage.getItem("token")) {
        return true
    } else {
        return false
    }
}

// removes an OBJECT from local storage
export function removeStorageItem(name) {
    localStorage.removeItem(name);
}

// returns an OBJECT of the item in local storage depending on its KEY
export function getStorageItem(name) {
    return JSON.parse(localStorage.getItem(name));
}

// stores an OBJECT into local storage assigned to a KEY
export function setStorageItem(name, data) {
    localStorage.setItem(name, JSON.stringify(data));
}

export function evaluateLogin(response) {
    if (response === "\"email\" is not allowed to be empty") return "Vetky polia musia by vyplnen";
    if (response === "\"password\" is not allowed to be empty") return "Vetky polia musia by vyplnen";

    if (response === "\"email\" length must be at least 6 characters long") return "E-mail je prli krtky";
    if (response === "\"password\" length must be at least 6 characters long") return "Heslo je prli krtke";

    if (response === "\"email\" must be a valid email") return "E-mail neexistuje";
    if (response === "Email is invalid") return "Pouvate s danm e-mailom neexistuje";

    if (response === "Password is invalid") return "Nesprvne heslo";

    if (response === "Login successful") return "spene ste sa prihlsili";

    return "Nastala chyba pri prihlasovan. Niektor daje mu by v zlom formte"
}

export function evaluateRegister(response) {
    if (response === "Password is in an invalid format") return "Heslo je prli jednoduch. Heslo mus by dlh aspo 8 znakov a obsahova aspo jedno vek psmeno a aspo jedno slo.";
    
    if (response === "\"name\" is not allowed to be empty") return "Vetky polia musia by vyplnen";
    if (response === "\"email\" is not allowed to be empty") return "Vetky polia musia by vyplnen";
    if (response === "\"phone\" is not allowed to be empty") return "Vetky polia musia by vyplnen";
    if (response === "\"address\" is not allowed to be empty") return "Vetky polia musia by vyplnen";
    if (response === "\"psc\" is not allowed to be empty") return "Vetky polia musia by vyplnen";
    if (response === "\"city\" is not allowed to be empty") return "Vetky polia musia by vyplnen";
    if (response === "\"country\" is not allowed to be empty") return "Vetky polia musia by vyplnen";

    if (response === "Phone number already exists") return "Telefnne slo sa u pouva inm tom";
    if (response === "Email already exists") return "Zadan e-mail je u zaregistrovan";

    if (response === "The user has been successfully registered") return "spene ste sa zaregistrovali";

    if (response === "\"email\" must be a valid email" || "\"email\" length must be at least 6 characters long") return "Zadan e-mail neexistuje"

    return "Nastala chyba pri registrci. Niektor daje mu by v zlom formte"
}

// adds an ITEM and its AMOUNT into the CART
export function addToCart(productId, amount, parent) {
    var cart = getStorageItem("cart");
        
    for (let i = 0; i <cart.length; i++) {
        if (cart[i]._id === productId) {
            cart[i].amount = cart[i].amount + amount
            break;
        } else {
            if (i === cart.length - 1) {
                cart.push({
                    _id: productId,
                    amount: amount
                });

                break;
            }
        }
    }

    if (cart.length === 0) {
        cart.push({
            _id: productId,
            amount: amount
        })
    }

    setStorageItem("cart", cart);
    parent.props.history.push("/kosik");
}

// show and hide mobile menu
export function changeMenu() {
    var menu = document.getElementById("mobile-menu");
    var header = document.getElementById("header");

    var style = window.getComputedStyle(menu);
    var display = style.getPropertyValue("display");

    if (display === "none") {
        menu.style.display = "flex";
        setTimeout(() => {
            menu.style.opacity = 1;
        }, 1);

        document.getElementById("menu-line-1").style.width = "90%";
        document.getElementById("menu-line-2").style.width = "70%";
        document.getElementById("menu-line-3").style.width = "50%";

        //document.getElementById("menu-line-1").style.backgroundColor = "#A161B3";
        //document.getElementById("menu-line-2").style.backgroundColor = "#A161B3";
        //document.getElementById("menu-line-3").style.backgroundColor = "#A161B3";
    } else  {
        menu.style.opacity = 0;
        setTimeout(() => {
            menu.style.display = "none";
        }, 301);

        document.getElementById("menu-line-1").style.width = "100%";
        document.getElementById("menu-line-2").style.width = "100%";
        document.getElementById("menu-line-3").style.width = "100%";

        //document.getElementById("menu-line-1").style.backgroundColor = "#383838";
        //document.getElementById("menu-line-2").style.backgroundColor = "#383838";
        //document.getElementById("menu-line-3").style.backgroundColor = "#383838";
    }
}